// pages/api/players.js
import fs from 'fs';
import path from 'path';

const playersFilePath = path.join(process.cwd(), 'data', 'players.json');

// ËØªÂèñÈÄâÊâãÊï∞ÊçÆ
function readPlayersData() {
  if (!fs.existsSync(playersFilePath)) {
    // ËøîÂõûÈªòËÆ§ÈÄâÊâãÊï∞ÊçÆ
    return {
      players: [
        {
          id: 1,
          nickname: 'ÊöóÂ§úÁåéÊâã',
          game_id: 'NightHunter',
          group_nickname: 'ÁåéÊâã',
          score: 15000,
          positions: ['‰ºòÂäøË∑Ø'],
          heroes: [],
          win_rate: 60,
          championships: 2,
          synergy_players: []
        }
      ]
    };
  }
  const data = fs.readFileSync(playersFilePath, 'utf8');
  return JSON.parse(data);
}

export default function handler(req, res) {
  // Á°Æ‰øùÊï∞ÊçÆÁõÆÂΩïÂ≠òÂú®
  const dataDir = path.join(process.cwd(), 'data');
  if (!fs.existsSync(dataDir)) {
    fs.mkdirSync(dataDir);
  }

  switch (req.method) {
    case 'GET':
      try {
        const playersData = readPlayersData();
        res.status(200).json(playersData);
      } catch (error) {
        res.status(500).json({ error: 'Failed to fetch players' });
      }
      break;

    default:
      res.setHeader('Allow', ['GET']);
      res.status(405).end(`Method ${req.method} Not Allowed`);
  }
}
// pages/api/teams.js
import fs from 'fs';
import path from 'path';

const teamsFilePath = path.join(process.cwd(), 'data', 'teams.json');

// ËØªÂèñÂõ¢ÈòüÊï∞ÊçÆ
function readTeamsData() {
  if (!fs.existsSync(teamsFilePath)) {
    return [];
  }
  const data = fs.readFileSync(teamsFilePath, 'utf8');
  return JSON.parse(data);
}

// ÂÜôÂÖ•Âõ¢ÈòüÊï∞ÊçÆ
function writeTeamsData(teams) {
  fs.writeFileSync(teamsFilePath, JSON.stringify(teams, null, 2));
}

export default function handler(req, res) {
  // Á°Æ‰øùÊï∞ÊçÆÁõÆÂΩïÂ≠òÂú®
  const dataDir = path.join(process.cwd(), 'data');
  if (!fs.existsSync(dataDir)) {
    fs.mkdirSync(dataDir);
  }

  switch (req.method) {
    case 'GET':
      try {
        const teams = readTeamsData();
        res.status(200).json({ teams });
      } catch (error) {
        res.status(500).json({ error: 'Failed to fetch teams' });
      }
      break;

    case 'POST':
      try {
        const teams = readTeamsData();
        const newTeam = {
          id: Date.now(),
          name: req.body.name || `Èòü‰ºç${teams.length + 1}`,
          players: []
        };
        teams.push(newTeam);
        writeTeamsData(teams);
        res.status(201).json({ team: newTeam });
      } catch (error) {
        res.status(500).json({ error: 'Failed to create team' });
      }
      break;

    default:
      res.setHeader('Allow', ['GET', 'POST']);
      res.status(405).end(`Method ${req.method} Not Allowed`);
  }
}
